import { Link } from "react-router-dom";

const Previous = (props) => {
    const id = parseInt(props.id);
    return(
        <Link to={
            {
                pathname: `/details/${id===1 ? id : id-1}`,
                state:  {characters : props.character} 
            }
        }
            className="bottom-navigation">
            <p>Previous</p>
        </Link>
    )
}

export default Previous;