import React from 'react'
import { Link } from 'react-router-dom'

const Next = (props) => {
    const id = parseInt(props.id);
    return(
        <Link to={
            {
             pathname:`/details/${id+1}`, 
             state: {characters : props.character}
            }
        }  className="bottom-navigation">
            <p>Next</p>
        </Link>
    )
}

export default Next;
